<template>
  <div>
    <el-card>
      <div slot="header">
        <span>技术博客</span>
      </div>
      <div v-for="each in articleList" :key="each.id">
        <el-link @click="jumpArticle(each.id)">{{each.title}}</el-link>
        <el-divider></el-divider>
      </div>
      <el-pagination background layout="prev,pager,next" @current-change="getList" :page-count="pages">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'ArticleList',
  data () {
    return {
      title: '',
      pages: 0,
      articleList: []
    }
  },
  created: function () {
    this.getList(1)
  },
  methods: {
    jumpArticle: function (id) {
      this.$router.push({
        path: `/article/${id}`
      })
    },
    getList: function (currentPage) {
      let params = {
        'currentPage': currentPage,
        'articleType': this.$route.params.articleType
      }
      this.$axios.get('/list/', {params: params}).then(res => {
        this.articleList = res.data.list
        this.pages = res.data.count
        console.log(this.articleList)
      })
    }
  }
}
</script>

<style scoped>
  div{
    /*margin: 0 100px;*/
  }
  span {
    font-size: 20px;
  }
  .el-link{
    /*margin-bottom: 15px;*/
    font-size: 16px;
    margin-left: 20px;
  }
</style>



