<template>
  <div>
    <div v-for="each in commentList" :key="each.id" style="margin-top: 15px">
      <el-card>
        <div slot="header" style="height: 20px" >
          <span>{{each.content_id}}楼</span>
          <el-button size="mini" type="default" style="float: right; padding: 6px 15px" @click="moreComment(each)">查看回复</el-button>
        </div>
        <vue-markdown :source="each.content"></vue-markdown>
      </el-card>
    </div>
    <el-dialog :visible.sync="dialogVisible" width="80%" title="查看回复">
      <div v-for="each in secComent" :key="each.id" style="margin-bottom: 15px">
        <el-card>
          <div slot="header" style="height: 20px" >
            <span>第{{each.content_id}}个回复</span>
          </div>
          <vue-markdown :source="each.content"></vue-markdown>
        </el-card>
      </div>
      <span style="font-size: 18px">添加评论</span>
      <mavon-editor v-model="newSecComment"></mavon-editor>
      <el-button type="primary" @click="commit()" style="margin-top: 15px">确定</el-button>
    </el-dialog>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
export default {
  name: 'Comment',
  components: {
    VueMarkdown
  },
  props: {
    commentList: Array
  },
  data () {
    return {
      dialogVisible: false,
      secComent: [
        {'id': 1, 'content': '## 123'},
        {'id': 2, 'content': '## Hello World'},
        {'id': 3, 'content': '## 123'}
      ],
      newSecComment: '',
      firstCommentId: ''
    }
  },
  methods: {
    moreComment: function (comment) {
      let params = {
        'first_comment_id': comment.id
      }
      this.firstCommentId = comment.id
      this.$axios.get('/article/seccomment/', {params: params}).then(res => {
        this.secComent = res.data.list
        this.dialogVisible = true
      })
    },
    commit: function () {
      let params = {
        'first_comment_id': this.firstCommentId,
        'content': this.newSecComment
      }
      this.$axios.post('/article/seccomment/', params).then(res => {
        this.newSecComment = ''
        this.dialogVisible = false
        this.$message.success('评论成功')
      })
    }
  }
}
</script>

<style scoped>

</style>



