<template>
  <el-container>
    <el-header>
      <web-header></web-header>
    </el-header>
    <el-main>
      <router-view></router-view>
    </el-main>
    <el-footer id="foot">
      <div style="margin: 0 300px 20px; font-size: 30px">GET IN TOUCH</div>
