      <font-awesome-icon :icon="['fab', 'twitter']" size="5x"></font-awesome-icon>
      <font-awesome-icon :icon="['fab', 'zhihu']" size="5x" @click="jump('https://www.zhihu.com/people/unreal-98')"></font-awesome-icon>
      <font-awesome-icon :icon="['fab', 'instagram']" size="5x"></font-awesome-icon>
    </el-footer>
  </el-container>
</template>

<script>
import WebHeader from './WebHeader'
export default {
  name: 'Home',
  components: {WebHeader},
  methods: {
    jump: function (url) {
      window.location.href = url
    }
  }
}
</script>

<style scoped>
  /*.el-container {*/
  /*  padding: 0 150px;*/
  /*}*/
  .el-main {
    margin-top: 60px;
    margin-left: 250px;
    margin-right: 250px;
  }
  .el-header {
    /*margin-left: 100px;*/
    /*margin-right: 100px;*/
    padding: 0 270px;
  }
  .el-footer {
    padding: 100px 500px 200px;
    background-color: #F6F6F6;
  }
  .el-footer svg {
    margin: 0 50px;
  }

</style>



