<template>
  <el-col :span="8">
    <el-card style="height: 383px">
      <div slot="header">
        <span>{{title}}</span>
        <el-button size="mini" type="default" style="float: right; padding: 6px 15px" @click="jumpMore">更多</el-button>
      </div>
      <div v-for="each in articleList" :key="each.id">
        <el-link @click="jumpArticle(each.id)">{{each.title}}</el-link>
      </div>
    </el-card>
  </el-col>
</template>

<script>
export default {
  name: 'WebCard',
  props: {
    title: String,
    articleList: Array,
    articleType: String
  },
  methods: {
    jumpArticle: function (id) {
      this.$router.push({
        path: `/article/${id}`
      })
    },
    jumpMore: function () {
      this.$router.push({
        path: `/article/list/${this.articleType}`
      })
    }
  }
}
</script>

<style scoped>
  .el-link {
    /*font-size: 16px;*/
    margin-bottom: 15px;
  }
</style>



