<template>
  <div class="markdown-body">
    <vue-markdown :source="text"></vue-markdown>
    <mavon-editor v-model="newComment"></mavon-editor>
    <el-button type="primary" @click="commit" style="margin-left: 50px; margin-top: 15px">确定</el-button>
    <comment :comment-list="commentList"></comment>
    <el-backtop></el-backtop>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import Comment from './Comment'
export default {
  name: 'Article',
  components: {
    VueMarkdown,
    Comment
  },
  data () {
    return {
      text: '',
      newComment: '',
      commentList: []
    }
  },
  created: function () {
    let params = {
      'article_id': this.$route.params.id
    }
    this.$axios.get('/detail/', {params: params}).then(res => {
      this.text = res.data.detail.content
    })
    this.$axios.get('/article/comment/', {params: params}).then(res => {
      this.commentList = res.data.list
    })
  },
  methods: {
    commit: function () {
      let params = {
        'article_id': this.$route.params.id,
        'content': this.newComment
      }
      this.$axios.post('/article/comment/', params).then(res => {
        this.$axios.get('/article/comment/', {params: params}).then(res => {
          this.commentList = res.data.list
        })
        this.newComment = ''
        this.$message.success('评论成功')
      })
    }
  }
}
</script>

<style scoped>
div {
  margin-left: 50px;
  margin-right: 50px;
}</style>



