<template>
  <div>
    <el-row :gutter="40">
      <el-col :span="16">
        <el-carousel height="383px">
          <el-carousel-item v-for="each in url" :key="each">
            <el-image :src="each" :fit="fit" style="height: 100%"></el-image>
          </el-carousel-item>
        </el-carousel>
      </el-col>
      <web-card title="最新文章" :article-list="latestList" article-type="latest"></web-card>
    </el-row>

    <el-row :gutter="40" style="margin-top: 100px">
      <web-card title="机器学习" :article-list="mlList" article-type="0"></web-card>
      <web-card title="深度学习" :article-list="dlList" article-type="1"></web-card>
      <web-card title="精选文章" :article-list="recommendList" article-type="recommend"></web-card>
    </el-row>
  </div>
</template>

<script>
import WebCard from './WebCard'
export default {
  name: 'Outline',
  components: {WebCard},
  data () {
    return {
      url: [
        // require('../assets/4.png'),
        // require('../assets/5.png'),
        // require('../assets/6.png'),
        // require('../assets/7.png'),
        require('../assets/8.png'),
        require('../assets/11.png'),
        require('../assets/12.png')
      ],
      fit: 'scale-down',
      dlList: [],
      mlList: [],
      latestList: [],
      recommendList: []
    }
  },
  created: function () {
    this.$axios.get('/card/list/', {params: {'articleType': 0}}).then(res => {
      this.mlList = res.data.list
    })
    this.$axios.get('/card/list/', {params: {'articleType': 1}}).then(res => {
      this.dlList = res.data.list
    })
    this.$axios.get('/card/list/', {params: {'articleType': 'latest'}}).then(res => {
      this.latestList = res.data.list
    })
    this.$axios.get('/card/list/', {params: {'articleType': 'recommend'}}).then(res => {
      this.recommendList = res.data.list
    })
  }
}
</script>

<style scoped>

</style>



