<template>
  <el-menu mode="horizontal">
    <el-menu-item index="4" @click="toMain">我的主页</el-menu-item>
    <el-menu-item index="1" @click="toArticalList(1)">深度学习</el-menu-item>
    <el-menu-item index="2" @click="toArticalList(0)">机器学习</el-menu-item>
    <el-menu-item index="3" @click="toContact">联系方式</el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: 'WebHeader',
  methods: {
    toArticalList: function (articleType) {
      this.$router.push({
        path: `/article/temp/${articleType}`
      })
    },
    toContact: function () {
      document.getElementById('foot').scrollIntoView()
    },
    toMain: function () {
      this.$router.push({
        path: `/`
      })
    }
  }
}
</script>

<style scoped>
  .el-menu-item {
    font-size: 22px;
    padding-left: 40px;
    padding-right: 40px;
  }
</style>



